
.carousel-caption>h3{
    color:whitesmoke;
    background-color: #100c08;
    font-size: 1.75rem;
    font-family: 'Comic Sans ms';
}

.carousel-item>img{
    margin-top: 3vh;
    height: 50vh;
    width: 100vw;
    object-fit: cover;
}

#root{
    background-color: #100c08;
}
.carousel{
    margin-bottom: 2vh;
}
.card-body{
    background-color:#100c08;
    color: whitesmoke;
}
.card-img-top{
    height: 300px;
    object-fit: contain;
    background-color:#100c08;
}

.card-title>span, .card-subtitle>span, .card-text>span{
    color:#dc3545;
    font-family: 'Comic Sans ms';
}

.modal-content>.modal-header, .modal-content>.form, .modal-content>div{
    background-color:#100c08;
    color:whitesmoke;
    border: 4px groove whitesmoke;
}

.card-body>div{
    display: inline-block;
    margin: 4px;
}



#root > div > div:nth-child(2) > button{
    margin-bottom: 10px;
}





